body {
  margin: 0;
  padding: 0;
  font-family: Nunito;
  background-color: #ebe7e7; }

header {
  margin: auto;
  background-color: #d6d3d3;
  margin-bottom: 5%;
  height: 100px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: -ms-flexbox;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1; }

header h1 {
  font-family: "Dancing Script", cursive;
  margin: auto;
  width: 280px; }

header h1 i {
  margin-right: 8%; }

li {
  padding: 20px;
  width: 310px;
  margin: 0% auto 3%;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: white;
  color: #555b68;
  list-style: none; }



